.TimeZone {
  .favItemActionBtn {
    font-size: 0.65rem;
    line-height: 0.75rem;
    @apply col-span-3 first:rounded-bl-lg first:col-span-4 border-r-2 last:border-r-0 last:rounded-br-lg p-1;
    @apply text-slate-400 tracking-widest;
    @apply bg-gradient-to-t from-neutral-200;
    @apply hover:bg-gradient-to-b hover:cursor-pointer;
    @apply active:bg-gradient-to-b active:cursor-pointer active:animate-bounce;
  }
}
